@charset "utf-8";

/* App.css */
.load_img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* pc 버전 */
@media screen and (min-width: 768px) {
  header{
    position: fixed;
    top: 0;
    left: 0;
    background: #ccc;
    width: 180px;
    height: 100vh;
  }
  header h1{
    text-align: center;
    padding: 15px 0;
  }
  main{
    position: absolute;
    width: calc(width -180);
    left: 180px;
    top: 0;
    padding: 30px;
    box-sizing: border-box;
  }
}

/* 모바일 버전 */
@media screen and (max-width: 767px) {
  header{
    position: absolute;
    width: 100%;
    top: 0;
  }
  header h1{
    text-align: center;
  }
}